<template>
    <div class="detail-content">
        <div class=header>
            <div class="content" ref="mainHead">
                <i class="anticon icon-back" @click="back"></i>
            </div>
        </div> 
        <div ref="scrollContent">
            <slider></slider>
            <!-- <activity-pannel></activity-pannel>
            <product-pannel></product-pannel> -->
            <div class="coupon-wrapper" v-if="isCoupon && 2!==activityType">
                <div class="coupon-icon"></div>
                <div class="coupon-text"><div class="word-ellipsis-1">下单后邀请好友，两人同享立减优惠</div></div>
                <i class="anticon icon-information"></i>
            </div>
            <div class="ip-brand">
                <div>
                    <span class="brand">品牌</span>
                    <span class="word-ellipsis-1">机动战士高达SEED DESTINY</span>
                    <i class="anticon icon-go"></i>
                </div>
                <div>
                    <span class="brand">品牌</span>
                    <span class="word-ellipsis-1">机动战士高达SEED DESTINY 机动战士高达SEED DESTINY</span>
                    <i class="anticon icon-go"></i>
                </div>
            </div>
            <div class="ensure-wrapper">
                <div style="width:100%;height:66px;background-color:#eee;margin: 16px 0;"></div>
            </div>
            <block title="商品详情">
                <div style="padding:6px 12px 12px;">
                    <div style="width:100%;height:100px;background-color:#eee;"></div>
                </div>
                 
            </block>
            <block title="商品详情">
                 <div style="padding:6px 12px 12px;">
                    <div style="width:100%;height:230px;background-color:#eee;"></div>
                </div>
            </block>
            <block title="商品详情">
                    <div style="width:100%;height:230px;background-color:#e3e3e3;"></div>
                    <div style="width:100%;height:530px;background-color:#7f7f7f;"></div>
                    <div style="width:100%;height:130px;background-color:#f8f8f8;"></div>
                    <div style="width:100%;height:630px;background-color:#c9c9c9;"></div>
            </block>
            <block title="商品详情">
                <div style="padding:6px 12px 12px;">
                    <div style="width:100%;height:300px;background-color:#eee;"></div>
                </div>
                 
            </block>
            <block title="商品详情">
                <div style="padding:6px 12px 12px;">
                    <div style="width:100%;height:340px;background-color:#eee;"></div>
                </div>
                 
            </block>
            <div class="bottom-copy"></div>
        </div>
        <bottom @add="onAddCart"></bottom>
        <sku-dialog :visible.sync="skuVisible" :detail="{}" @show="()=>{skuVisible = true}" @close="()=>{skuVisible = false}"></sku-dialog>
    </div>
    
</template>
<script>
import slider from '../../components/Slider/Main'
import block from '../../components/Block/Block'
import bottom from '../../components/Bottom/Bottom'
//import activityPanel from '../../components/ActivityPanel/ActivityPanel'
import productPanel from '../../components/ProductPanel/ProductPanel'
import skuDialog from '../../components/SkuDialog/SkuDialog'
import * as utils from '../../utils/utils'
export default {
    components: {
        slider,
        block,
        //activityPanel,
        productPanel,
        bottom,
        skuDialog,
    },
    data() {
        return {
            isGlobalError: false,
            isGlobalLoading: true,
            needRemind: false,
            isShowUpgradeRemind: false,
            dialogType: -1,
            skuVisible: false,
            isShowEnsure: false,
            activityType: 0,
            isCoupon:false,
            itemsId: null,
            isSaling: false,
            detail: {},
            imgs: [],
            hashControl: {
                hashElements: ['']
            },
            simpleAttrlist: [],
            infos: []
        }
    },
    created() {
        this.itemsId = utils.getQueryString("itemsId") || 0;
        if(0 === this.itemsId) {
            this.isGlobalLoading = false;
            this.isGlobalError = true
        }
        console.log(JSON.parse(console.log(localStorage[this.itemsId])))
        try {
            this.initDom(JSON.parse(localStorage[this.itemsId]), true, ()=> {
                /*this.getDetails().then(()=>{
                    this.initHashTag()
                })*/
            })
        }
        catch (e) {
            /*this.getDetails().then(() =>{
                this.initHashTag()
            })*/
        }
        
    },
    methods: {
        initDom(data, useLocalStorage = true, callback = function() {}) {

            console.log(data);
            data.status = data.saleStatus;
            this.isSaling = 1 == data.itemsStatus;
            const dataString = JSON.stringify(data)
            if(!data) return false;


            this.detail = data
            // 后台编写的html
            const mobileDesc = data.mobileDesc;
            //相关规格的描述
            data.attrList || (data.attrList = [])
            if(data.attrList.length > 0){
                // 初始显示4个
                this.simpleAttrlist = data.attrList.fitler((e, i)=> i < 3)  || []
            }
            let attrList = data.attrList.map((e)=>{
                const attrValue = e.attrValue;
                Array.isArray(attrValue) && (e.attrValue = attrValue.join("; "))
                return e
            })
            this.detail.ipRightName && (attrList = [...attrList, ...{
                attrName: "IP",
                attrValue: ipRightName
            }])
            this.detail.brandName && (attrList = [...attrList, ...{
                attrName: "品牌",
                attrValue: ipRightName
            }])
            this.infos = attrList
            // 头部商品主要图片
            this.imgs = data.imgs
        },
        getDetails() {
        },
        initHashTag() {
            // document.querySelectorAll() 返会的不是个数组类型
            this.hashControl.hashElements = [...document.querySelectorAll(".to-hash")]
        },
        back() {
            utils.back();
        },
        onAddCart(){
            if(this.needRemind) {
                this.isShowUpgradeRemind = true
                return false
            }
            console.log(123)
            this.dialogType = 0;
            this.skuVisible = true;
        },
        addProductDetailView() {
            this.addProductDetailView()
        }
    }
}
</script>
<style lang=scss>
.coupon-wrapper {
    display: flex;
    height: 44px;
    background-color: #FFF;
    position: relative;
    .coupon-icon {
        width: 38px;
        height: 14px;
        background: url(../../resoures/images/coupon.svg) no-repeat;
         margin: 15px 4px 0 12px;
    }
    .coupon-text {
        flex: 1;
        color: #4cc3ff;
        letter-spacing: 0;
        overflow: hidden;
        div {
            width: 200%;
            line-height: 88px;
            font-size: 28px;
            transform-origin: left top;
            transform: scale(.5);
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .icon-information {
        line-height: 44px;
        font-size: 16px;
        position: absolute;
        right: 12px;
        top: 0;
    }
}
.ip-brand {
    padding: 0 12px;
    position: relative;
    background: #fff;
    &:before{
        content: "";
        position: absolute;
        border-bottom: 1px solid #e7e7e7;
        transform: scaleY(.5);
        transform-origin: 0 0;
        top: 0;
        left: 12px;
        right: 12px;
    };
    >div {
        display: flex;
        height: 44px;
        align-items: center;
        font-size: 12px;
        position: relative;
        &:after{
            content: "";
            position: absolute;
            border-bottom: 1px solid #e7e7e7;
            transform: scaleY(.5);
            transform-origin: 0 0;
            bottom: 0;
            left: 0px;
            right: 0px;
        }
        &:last-of-type:after {
            display: none
        }
        span{
            &:first-of-type{
                width: 52px;
                font-size: 12px;
                color: #999;
                letter-spacing: 0;
            }
            &:last-of-type{
                flex: 1;
                font-size: 14px;

            }
        }
    }
}
.ensure-wrapper {
    padding: 0 12px;
    position: relative;
    background: #fff;
    overflow: hidden;
    &:before{
        content: "";
        position: absolute;
        border-bottom: 1px solid #e7e7e7;
        transform: scaleY(.5);
        transform-origin: 0 0;
        top: 0;
        left: 12px;
        right: 12px;
    };
}
.bottom-copy {
    height: 60px;
    margin-top: 10px;
}
</style>


