<template>
	<div class="slider-content">
		<ul class="slider-wrapper" ref="wrapper" :style="styleObj" 
			@touchstart="swipeStart" 
			@touchmove="swipeMove" 
			@touchend="swipeEnd" 
			@mousedown="swipeStart" 
			@mouseup="swipeEnd" 
			@mousemove="swipeMove" 
		>
			<template v-if="pages > 0">
				<li v-if="sliderInit.canLoop" class="slide" :style="itemObj(items[pages-1])">
					<div class="slide-img" :style="itemObj(items[pages-1])"></div>
				</li>
				<li v-else class="slide">
					<div class="empty-slide"></div>
				</li>

				<li class="slide" v-for="(item, index) in items" :key="index">
					<div v-if="index===0 && coverImage" class="cover-img" :style="itemObj({src: coverImage})"></div>
					<div class="slide-img" style="backgroundImage: url(//i0.hdslb.com/bfs/mall/mall/57/b2/57b29b4432d57534d4215f5d7c8b17f8.jpg@800w_800h.webp)"></div>
				</li>
				<li v-if="showLink" class="slide-scrol-link">
					
				</li>
				<li v-else class="slide">
					<div class="slide-img"  :style="itemObj(items[0])"></div>
				</li>

			</template>
			<template v-else>
				<li class="slide-img">
					<div class="empty-slide"></div>
				</li>
				<li class="slide-img">
					<div class="empty-slide"></div>
				</li>
			</template>
		</ul>
		<slider-pager :items="items" v-model="selectPager"></slider-pager>
	</div>
</template>
<script>
import sliderPager from '../../components/Slider/SliderPager'
let timer = null;
export default {
	props: {
        items: {
            type: Array,
            default: function() {
                return []
            }
        },
        coverImage: {
            type: String,
            default: function() {
                return ""
            }
        },
        isRight: {
            type: Boolean,
            default: function() {
                return !1
            }
        },
        value: {
            type: Number,
            default: function() {
                return 0
            }
        }
    },	
    components: {
		sliderPager
    },
    data() {
		return {
			isSwip: !1,
            selectPager: 0,
            showLink: !1,
            showEndTimer: 0,
            basicData: {
                isFirst: !0,
                startTime: 0,
                transitionEnding: !1,
                tracking: !1,
                posWidth: "0",
                posHeight: "0",
                start: {},
                end: {},
                begin: !0,
                isReplace: !1
            },
            sliderInit: {
                thresholdDistance: 50,
                currentPage: 0,
                canLoop: !1
            }
		}
	},
	mounted() {
		document.addEventListener("touchmove", this.globalTouch),
        this.basicData.isReplace = !0,
        this.sliderInit.currentPage = 1,
        this.slide(),
        this.beginAuto(!1)
	},
	methods: {
		touchStop: function(e) {
            e.preventDefault()
        },
		swipeStart(e) {

			this.basicData.begin = false
			if("touchstart" === e.type){
				if(e.touches.length > 1) return void (this.basicData.tracking = false)
				document.addEventListener("touchmove", this.touchStop)
				this.basicData.isFirst = true,
	            this.basicData.tracking = true,
	            this.basicData.startTime = (new Date).getTime(),
	            this.basicData.start.x = e.targetTouches[0].clientX,
	            this.basicData.start.y = e.targetTouches[0].clientY,
	            this.basicData.end.x = e.targetTouches[0].clientX,
	            this.basicData.end.y = e.targetTouches[0].clientY
			} else {
				document.addEventListener("touchmove", this.touchStop),
  				this.basicData.isFirst = true,
	            this.basicData.tracking = true,
                this.basicData.startTime = (new Date).getTime(),
                this.basicData.start.x = e.clientX,
                this.basicData.start.y = e.clientY,
                this.basicData.end.x = e.clientX,
                this.basicData.end.y = e.clientY
			}


		},
		swipeMove(e) {

			if(this.basicData.tracking) {
				if("touchmove" === e.type){
					this.basicData.end.x = e.targetTouches[0].clientX;
					this.basicData.end.y = e.targetTouches[0].clientY;
				} else {
					this.basicData.end.x = e.clientX;
					this.basicData.end.y = e.clientY;
				}



				let touchDistanceX = this.basicData.end.x - this.basicData.start.x
				let touchDistanceY = this.basicData.end.y - this.basicData.start.y
				//往右滑
				if(1 == this.sliderInit.currentPage && !this.sliderInit.canLoop && touchDistanceX > 0 ) {
					touchDistanceX = touchDistanceX > 101 ? 101 : touchDistanceX
				}
				//往左滑
				if(this.pages == this.sliderInit.currentPage && -touchDistanceX > 0 && Math.abs(touchDistanceY) < Math.abs(touchDistanceX)) {
					clearTimeout(this.showEndTimer);
					this.showLink = !0;
					touchDistanceX = -touchDistanceX > 101 ? -101 : touchDistanceX;
					this.$emit("transfrom-end-start", touchDistanceX)
				}

				if(!this.basicData.isFirst && this.isSwip) {
					e.preventDefault()
					e.stopImmediatePropagation()
					this.basicData.isReplace = true;
					this.basicData.posWidth = -this.currentWidth + touchDistanceX + 'px'

				} else {
					if(!this.basicData.isFirst) {
						document.removeEventLister("touchmove", this.touchStop)
						return false
					}
					if(Math.abs(touchDistanceY) < Math.abs(touchDistanceX)) {
						this.isSwip = true;
						this.basicData.isFirst = false;
						e.preventDefault()
						e.stopImmediatePropagation()
						this.basicData.isReplace = true
						this.basicData.posWidth = -this.currentWidth + touchDistanceX + 'px'
					} else {
						Math.abs(touchDistanceY) > 10 && (this.isSwip = false, this.isFirst = false)
						document.removeEventListener("touchmove", this.touchStop)
					}
				}

			}
		},
		swipeEnd(e) {
			this.isFirst = true;
			this.basicData.tracking = false;
			this.basicData.isReplace = false;
			document.removeEventListener("touchmove", this.touchStop)
			if(!this.isSwip) {
				this.isSwip = true;
				return false
			}
			this.isSwip = false;
			if(0!==this.items.length) {
				var e = this.basicData.end.x - this.basicData.start.x
                  , i = this.basicData.end.y - this.basicData.start.y;
                if (e > this.sliderInit.thresholdDistance && Math.abs(i) < 320)
                    this.pre();
                else if (-e > this.sliderInit.thresholdDistance) {
                    if (this.showLink) {
                        if (-e > 60)
                            this.slide(),
                            (new Date).getTime() - this.basicData.startTime < 300 || this.onTransformEnd();
                        else
                            this.slide();
                        return
                    }
                    if (Math.abs(i) < 320)
                        return void (console.log("swipeEnd"), this.next());
                    this.slide()
                } else
                    this.slide()
			}
		},
		pre: function() {
            if (0 != this.items.length)
                if (this.sliderInit.currentPage > 1)
                    this.sliderInit.currentPage -= 1,
                    this.slide();
                else if (1 == this.sliderInit.currentPage) {
                    if (!this.sliderInit.canLoop)
                        return this.slide(),
                        !1;
                    this.sliderInit.currentPage -= 1,
                    this.basicData.transitionEnding = !0,
                    this.slide()
                } else
                    this.slide()
        },
        next: function() {
            0 != this.items.length && (this.sliderInit.currentPage < this.pages ? (this.sliderInit.currentPage += 1,
            this.slide()) : this.sliderInit.currentPage == this.pages ? (this.sliderInit.currentPage += 1,
            this.basicData.transitionEnding = !0,
            this.slide()) : this.slide())
        },
        slide: function() {
            this.basicData.posWidth = -this.currentWidth + "px",
            this.showEndTimer = setTimeout(() => {
                this.showLink = false
            }, 300),
            this.basicData.begin = false
        },
        onTransformEnd: function() {
            this.$emit("transform-end")
        },
        beginAuto: function(auto = true) {
        	if(auto) {
				timer = setInterval(()=> {
					this.next();
				}, 5e3)
        	} else
        		clearInterval(timer)
        },
        globalTouch: function(t) {
            if (t.cancelable && (this.$refs.wrapper.contains(t.target) || this.$refs.wrapper == t.target)) {
                let touchDistanceX = this.basicData.end.x - this.basicData.start.x;
                let touchDistanceY = this.basicData.end.y - this.basicData.start.y;
                Math.abs(touchDistanceX) > 20 && Math.abs(touchDistanceY) < 50 && t.preventDefault()
            }
        },
		itemObj(item) {
			return {"background-image": "url('" + item.src + "')"}
		}
	},
	watch: {
        "basicData.begin": function(t) {
            this.beginAuto(t)
        },
        "sliderInit.currentPage": function(t) {
        	console.log(t)
            this.selectPager = t - 1
        },
        selectPager: function(t) {
            this.sliderInit.currentPage = t + 1,
            this.$emit("input", t),
            this.slide()
        }
    },
	computed: {
		pages() {
			return this.items.length
		},
		styleObj() {
						const obj = {};
			if(this.basicData.isReplace) {
				this.basicData.isReplace = false
			} else {
				obj.transition = "all 0.3s";
			}
			obj.transform = `translate3D(${this.basicData.posWidth}, ${this.basicData.posHeight}, 0)`;

			return obj
		},
		currentWidth: {
            get: function() {
                var t = this
                  , e = void 0
                  , w = void 0;
                this.basicData.transitionEnding && (0 == this.sliderInit.currentPage ? setTimeout(function() {
                    t.basicData.isReplace = !0,
                    t.sliderInit.currentPage = t.pages,
                    t.slide()
                }, 150) : setTimeout(function() {
                    t.basicData.isReplace = !0,
                    t.sliderInit.currentPage = 1,
                    t.slide()
                }, 150),
                this.basicData.transitionEnding = !1);
                var i = this.sliderInit.currentPage;
                for (var n in this.$el.children)
                    /slider-wrapper/gi.test(this.$el.children[n].className) && (e = this.$el.children[n]);
                e.children;


                window.innerWidth ? w = window.innerWidth : document.body && document.body.clientWidth && (w = document.body.clientWidth);
                document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth && ( w = document.documentElement.clientWidth);
                return i * window.innerWidth
            }
        },
        set: function(t) {
            return t
        }
    },
	beforeDestroy: function() {
        document.removeEventListener("touchmove", this.globalTouch)
    }

}
</script>
<style lang=scss>
.slider-content {
	width: 100%;
	overflow: hidden;
	position: relative;
	.slider-wrapper {
		position: relative;
	    width: 100%;
	    height: 100%;
	    z-index: 1;
	    display: flex;
	    transition-property: transform;
	    box-sizing: content-box;
		.slide {
			flex-shrink: 0;
		    width: 100%;
		    height: 100%;
		    position: relative;
		    transition-property: transform;
		    div {
		    	height: 100%;
			    width: 100%;
			    background-position: 50%;
			    background-size: cover;
			    background-repeat: no-repeat;
		    }

		}
	}
}
.empty-slide, .slide-img {
	padding-top: 100%;
}
</style>


