<template>
	<div class="popup" @click.stop @touchmove.stop.prevent>
		<transition name="popup-fade">
			<div v-show="visible" class="popup-mask" @click.stop="onClose" @touchmove.stop.prevent></div>
		</transition>
		<transition name="popup-fade">
			<div v-show="visible" class="popup-container-wrapper">
				<div class="popup-container">
					<div class="popup-close" @click.stop="onClose"><i class="anticon icon-close"></i></div>
					<div class="popup-image" :style="{backgroundImage: 'url('+popupImage+')'}"></div>
					<div class="popup-body">
						<slot></slot>
					</div>	
				</div>
				
			</div>
		</transition>
	</div>
</template>
<script>
export default {
	props: {
		visible: {
			type: Boolean,
			default: false
		},
		image: {
			type: String,
			default: ""
		}
	},
	computed: {
		popupImage() {
			if(this.image) {
				return this.image
			} else 
				return this.image
		}
	},
	methods: {
		onClose() {
			this.$emit("close")
		}
	}
}
</script>
<style lang="scss">
.popup{


	.popup-mask {
		position: fixed;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    background: #000;
	    opacity: .5;
	    z-index: 10000;
	}
	.popup-container-wrapper {
		position: fixed;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    background: transparent;
	    z-index: 10001;
		.popup-container {
			position: relative;
		    margin-left: 12px;
		    margin-right: 12px;
		    margin-bottom: 12px;
		    background: #fff;
		    border-radius: 4px;
			.popup-close {
		    	width: 40px;
		    	height: 40px;
		    	position: absolute;
		    	top: -20px;
		    	right: 12px;
		    	background: #f5f5f5;
		    	border-radius: 40px;
		    	box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
		    	z-index: 10004;
		
		    }
		    .popup-image {
		    	position: absolute;
			    top: -31px;
			    left: 12px;
			    width: 100px;
			    height: 100px;
			    background: #fff;
			    box-shadow: 0 2px 6px 0 rgba(0,0,0,.3);
			    border-radius: 4px;
			    z-index: 10004;
			    background-size: cover;
			    background-position: 50%;

		    }
		    .popup-body {
		    	width: 100%;
		    	height: 100%;
		    	position: relative;
		    	overflow: auto;
		    }
		}
	    
	}
	.popup-fade-enter-active,.popup-fade-leave-active{
	    transition:opacity .2s
	}
	.popup-fade-enter,.popup-fade-leave-to{
	    opacity:0
	}
}


</style>


