<template>
	<div class="slider-pager">
		<span class="slider-pager-bullet">{{selectIndex | showpager(pages) }}/{{pages}}</span>
	</div>
</template>
<script>
export default {
	data() {
		return {
			selectIndex: 0
		}
	},
	props: {
		items: {
			default: ()=>[],
			type: Array
		},
		value: {
			default: 0,
			type: Number
		}
	},
	filters: {
		showpager(index, pages) {
			return -1 === index ? pages : index + 1
		}
	},
	watch: {
		value(t) {
			this.selectIndex = t
		}
	},
	methods: {
		changePage(t) {
			this.selectIndex = t;
			this.$emit("input", t)
		}
	},
	computed: {
		pages(){
			return this.items.length
		}
	}
}
</script>
<style lang=scss>
	.slider-pager{
		position: absolute;
	    bottom: 10px;
	    height: 18px;
	    width: 100%;
	    opacity: .6;
	    text-align: center;
	    transform: translateZ(0);
	    z-index: 2;
	    .slider-pager-bullet {
	    	display: inline-block;
		    vertical-align: top;
		    height: 36px;
		    width: 88px;
		    border-radius: 60px;
		    color: #212121;
		    letter-spacing: .42px;
		    font-size: 24px;
		    line-height: 36px;
		    background: #fff;
		    transform-origin: left top;
		    transform: scale(.5) translateX(50%);
	    }
	}
</style>


