<template>
	<popup :visible="visible"  @close="onClose" image="https://i0.hdslb.com/bfs/mall/mall/55/48/554878ac088bde6996fb51214323e273.png@200w_200h.webp">
		<div class="info">
			<div class="advance-price">
				<div class="price">
					<span class="symbol">￥</span>
					<span class="num">289<span class="split-line">-</span>2899</span>
				</div>
			</div>
			
			<div class="stock">库存充足</div>
			<div class="support-return">不支持7天无理由退换货</div>
		</div>
		<div class="sku-container">
			<div class="spec-container"  v-for="(key,specsIndex) in specs">
				<h4 v-if="!detail.isSpecHide || specs.length > 1">{{key}}</h4>
				<div>
					<div class="spec spec-normal" :class="getClass(index, specsIndex, spec)"  v-for="(spec, index) in showSpecObj[key]" @dblclick.stop >{{spec}}</div>
				</div>
				
			</div>
			<div class="spec-container spec-purchase-container">
				<h4>数量</h4>
				<div class="purchase-wrapper">
					<i class="anticon icon-purchase-control icon-reduce_b" :class="subtractClass" @click="addPurchaseCount(-1)"></i>
					<span class="num">{{purchaseCount}}</span>
					<i class="anticon icon-purchase-control icon-add_b"   @click="addPurchaseCount(1)"></i>
				</div>
			</div>
		</div>
		<div class="add-to-cart">
			<div class="btn-add-to-cart">加入购物车</div>
		</div>


	</popup>
</template>
<script>
import popup from "../../components/Popup/PopupInDetail"
import api from '../../models/api'
export default {
	data(){
		return {
			purchaseCount: 1,
			isWaitting: false,
			showAlert: false,
			skuObj: {},
			showSpecObj: {},
			showAlertCart: false,
			stockTips: ["库存充足", "库存紧张", "暂无库存", "已售罄"],
			isPopLoading: false,
			itemsSkuList: [],
			errorType: 0,
			specs: [],
			originPrice: 0,
			originStock: 0,
			selectSpecArray: [],
			skuResult: {},
			skuData: null,
		}

	},
	props: ["itemsId", "visible", "oriImg", "type", "detail", "restriction", "activityType", "timeNodeObj"],

	components: {
		popup,
	},
	computed: {
		subtractClass() {
			let disable = false;
			if(1 === this.purchaseCount ||0 === this.purchaseCount){
				disable = true;
			}
			return {
				disable
			}
		}
	},
	watch: {
		activityType() {
			if(this.skuData) {
				this.initSku(this.skuData)
				this.getSkuList()
			}
		},
		visible(t) {
			// 数据在watch activityType时获取了，这里判断没有获取数据时在次请求，已有数据则不请求
			if(t && 0 === this.itemsSkuList.length) {
				this.getSkuList()
			}
		}
	},
	methods: {
		getSkuKeys(itemsSkuList) {
			let keys = []
			let stock = 0; // 库存
			let low = 0;
			let hight = 0;
			let origin = 0;
			let price = 0;
			console.log(itemsSkuList)
			for(let i=0; i< itemsSkuList.length; i++) {
				const item = itemsSkuList[i]
				const specValues = item.specValues;
				keys[i] = specValues.join("&_;")
				this.skuObj[keys[i]] = item;
				stock += item.stock;
				let price = parseFloat(item.price)
				0 === i && (hight = low = price); // 初始时设置全部相等
				price < low && (low = price) // 比较上一个，小于上一个的设置更小
				price > hight && (hight = price) // 比较上一个，大于上一个的设置更大
				
				
			}

			this.originPrice = low === hight ? parseFloat(low) : parseFloat(low) + '-' + parseFloat(hight)
			this.originStock = stock;
			console.log(this.originPrice)
			return keys
		},
		getSkuList(skuNum = false){
			if(!skuNum && 0 === this.itemsSkuList) {
				this.isPopLoading = true;
				this.errorType = 0
			}
			const parmas = {
				itemsId: this.itemsId,
				activityId:  this.detail.activityInfoVO ? this.detail.activityInfoVO.activityId : 0
			}
			console.log("get")
			api.getSkus({
				parmas
			}).then(({data, status, msg})=>{
				
				this.isPopLoading = false;
				this.specs = data.specs;
				this.itemsSkuType = 4;
				this.skuData = data;
				this.initSku(data)
			}).catch(()=>{
				this.errorType = 1;
				this.isPopLoading = false;
				this.PopupError = true
			})
			
		},
		initSku(data) {
			
			this.itemsSkuList = data.itemsSkuList
			let keys = this.getSkuKeys(data.itemsSkuList)
			//'xx&_;xx&_;xx'
			console.log(keys)

			this.specs.forEach((e, index)=>{
				this.selectSpecArray[index] = this.selectSpecArray[index]
			})
			for(let i =0; i< keys.length; i++) {
				const key = keys[i]; 
				const item = this.skuObj[key];
				const specValues = key.split("&_;");
				for(let n =0; n<specValues.length; n++) {
					let spec = this.showSpecObj[data.specs[n]]
					spec = spec ? [...new Set([...spec,...[specValues[n]]])] : [specValues[n]]
					this.showSpecObj[data.specs[n]] = spec
				}
				this.skuResult[specValues.join("&_;")] = {
					activityType: 0,
					count: item.stock,
					price: [item.price]
				}
			}
			console.log(1,this.showSpecObj)
			let selectSpecArray = JSON.parse(JSON.stringify(this.selectSpecArray))
			let newArr = new Array(selectSpecArray.length);
			let index = 0;
			for(let u in  this.showSpecObj){
				let obj = this.showSpecObj[u]
				// 只有一个时默认第一个选中
				1 === obj.length &&  !selectSpecArray[index] && (selectSpecArray[index] = obj[0]) ;
				let r = this.skuResult[selectSpecArray.join("&_;")]
				r && 0 !== r.count &&(newArr[index] = selectSpecArray[index])
				index++
			}
			let r = this.skuResult[newArr.join("&_;")]
			r && 0 !== r.count ? this.selectSpecArray = newArr : this.selectSpecArray = new Array(this.specs.length)
			console.log(this.selectSpecArray)
		},
		initSkuObj() {
			this.image = this.skuObj.img;
			this.price = this.skuObj.price;
			this.purchaseCount = this.skuObj.skuNum
			this.stock = this.skuObj.stock;
			if(this.skuObj.specValues)
				this.skuSpec = this.skuObj.specValues.join(",")
			else if(this.detail.itemsInfo)
				this.skuSpec =  this.detail.itemsInfo.skuSpec
			else
				this.skuSpec = ""
		},
		getPreCreateInfo() {
			if(!this.isWaitting) {
				this.isWaitting = true;
			}
		},
		addPurchaseCount(num) {
			if(!(this.isWaitting || this.purchaseCount <= 1 && num < 0)){
				this.purchaseCount += num
			}
			
		},
		onClose(){
			this.$emit("update:visible", false)
		},
		getClass(index, specsIndex, spec) {
			let classname = {}
			let selectSpecArray = JSON.parse(JSON.stringify(this.selectSpecArray))
			selectSpecArray[specsIndex] = spec;
			const r = this.skuResult[selectSpecArray.join("&_;")];
			if(r && 0 !== r.count){
				classname["spec-active"] = true
			} else {
				classname["spec-disable"] = true
			}
			return classname
		}
	}
}
</script>
<style lang=scss scoped>
.info {
	margin-left: 126px;
    height: 52px;
    .advance-price{
    	margin-top: 18px;
    	height: 18px;
    	display: flex;
    	align-items: flex-end;
    	font-size: 0;
    }
	.price{
        font-size: 0;
        color: #4cc3ff;
        .symbol, .mini {
            font-size: 12px;
        }
        .num {
        	line-height: 18px;
            font-size: 18px;
            vertical-align: bottom;
            letter-spacing: 0;
            position: relative;
    		display: inline-block;
			.split-line{
				display: inline-block;
			    vertical-align: top;
			    margin: 0 2px;
			    overflow: hidden;
			}
        }
        .underline-price {
            margin-left: 4px;
            font-size: 12px;
            color: #999;
            text-decoration: line-through;
        }
    }
    .stock {
    	height: 12px;
	    margin-top: 6px;
	    font-size: 12px;
	    color: #999;
	    letter-spacing: 0.5px;
	    line-height: 12px;
    }
    .support-return {
    	margin-top: 4px;
	    height: 12px;
	    font-size: 12px;
	    color: #999;
	    letter-spacing: 0;
	    line-height: 12px;
    }
}
.add-to-cart {
	position: relative;
    bottom: 0;
    padding: 12px;
    width: 100%;
    height: 64px;
    background-image: linear-gradient(180deg,#e7e7e7,#e7e7e7 50%,transparent 0);
    background-size: 100% 1px;
    background-repeat: no-repeat;

    .btn-add-to-cart{
    	height: 100%;
	    background-color: #4cc3ff;
	    box-shadow: 0 2px 4px 0 rgba(28, 140, 193, 0.7);
	    border-radius: 36px;
	    color: #fff;
	    font-size: 14px;
	    text-align: center;
	    line-height: 40px;
    }
    &:before{
    	display: block;
	    content: "";
	    position: absolute;
	    border-bottom: 1px solid #e7e7e7;
	    transform: scaleY(.5);
	    transform-origin: 0 0;
	    top: 0;
	    left: 0;
	    right: 0;

    };
}
.sku-container {
	padding: 25px 10px 0;
    width: 100%;
    max-height: 353px;
    overflow: auto;
}
.spec-container {
    padding: 10px 0 0;
    position: relative;
    h4 {
    	margin: 0 0 10px;
	    font-size: 14px;
	    color: #212121;
	    font-weight: 400;
    }
    .spec {
    	position: relative;
    	margin: 0 10px 10px 0;
    }
    &:after{
		content: "";
	    width: 100%;
	    position: absolute;
	    border-bottom: 1px solid #e7e7e7;
	    transform: scaleY(.5);
	    transform-origin: 0 0;
	    bottom: 0;
	    left: 0;
	};
}
.spec-purchase-container {
	display: flex;
	justify-content: space-between;
	overflow: hidden;
    padding: 6px 0;
	h4 {
		display: inline-block;
	    margin: 0;
	    height: 32px;
	    line-height: 32px;
		
	}
	&:after{
		display: none
	};

	
	
	.purchase-wrapper{
		display: flex;
		.icon-purchase-control{
			font-size: 32px;
			margin-right: 10px;
		    color: #4cc3ff;
		    letter-spacing: 0;
		    line-height: 32px;
		    text-align: center;
			&:last-of-type{
				margin-right: 0;
			}
			&.disable {
				color: #ccc
			}
		}
		
		.num {
			font-size: 15px;
			margin-right: 10px;
		    color: #212121;
		    letter-spacing: 0;
		    line-height: 32px;
		    text-align: center;
		    min-width: 20px;

		}
	}
}
</style>


