<template>
	<div class="block">
		<div class="block-inner">
			<div class="block-header">
				<h2>{{title}}</h2>
			</div>
			<section class="block-content">
				<slot></slot>
			</section>
		</div>
	</div>
</template>
<script>
	export default {
		props: ["title"]
	}
</script>
<style lang=scss>
	.block {
    	overflow: hidden;
		position: relative;
	    margin-top: 10px;
	    box-sizing: border-box;
	    .block-inner {
	    	background: #fff;
	    	.block-header {
	    		position: relative;
			    padding: 16px 0 12px 12px!important;
			    font-size: 14px;
			    color: #212121;
			    line-height: 14px;
			    &:before{
			    	position: absolute;
				    left: 0;
				    top: 16px;
				    display: block;
				    width: 4px;
				    height: 14px;
				    content: "";
				    background: #4cc3ff;
				    border-radius: 0 2px 2px 0;
			    };
	    		h2 {
	    			font-size: 14px;
				    color: #212121;
				    font-weight: 700;
	    		}
	    	}
	    }
	}
</style>


