<template>
	<div class="bottom">
		<div>
			<div class="bottom-cart" >
				<i class="anticon icon-shopping-cart" @click="goCart"></i>
				<div class="cart-count">
					<span>10</span>
				</div>
			</div>
			<div class="bottom-add">
				<div class="bottom-btn-add" @click="onAddCart">加入购物车</div>
			</div>
			<div class="bottom-buy">
				<div class="bottom-btn-buy">立即购买</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	methods: {
		goCart() {
			window.location.href = location.origin + "/cart.html?shopId=" + this.shopId +"&isFromDetail=true"
		},
		onAddCart() {
			this.$emit("add")
		},
		onBuyNow() {
			//this.$emit("add")
		},
	}
}
</script>
<style lang=scss>
	.bottom {
		>div {
		position: fixed;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    width: 100%;
	    height: 60px;
	    background-color: #FFF;
	    box-shadow: 0 -1px 0 0 rgba(0,0,0,.08);
	    z-index: 100;
			display: flex;
			.bottom-cart {
				width: 58px;
    			position: relative;
				.icon-shopping-cart{
				    position: absolute;
				    font-size: 28px;
				    top: 16px;
				    left: 15px;
				}
				.cart-count{
					position: absolute;
				    top: 13px;
				    left: 29px;
				    background: #4cc3ff;
				    color: #fff;
				    text-align: center;
				    border-radius: 13px;
				    font-size: 20px;
				    height: 26px;
				    line-height: 26px;
				    letter-spacing: 0;
				    padding: 0 4px;
				    box-sizing: border-box;
				    transform-origin: 0 0;
				    transform: scale(.5);
				    min-width: 38px;
				}
			}
			.bottom-add{
				padding: 10px 6px 0;
				.bottom-btn-add {
					box-sizing: border-box;
				    border-radius: 31px;
				    border: 1px solid rgba(76,195,255,.4);
				    font-size: 14px;
				    color: #4cc3ff;
				    line-height: 38px;
				    text-align: center;
				}
			}
			.bottom-buy {
				padding: 10px 12px 0 6px;
				.bottom-btn-buy{
					white-space: nowrap;
				    background-image: -webkit-linear-gradient(326deg,#4cc3ff,#4cc3ff);
				    background-image: linear-gradient(-236deg,#4cc3ff,#4cc3ff);
				    box-shadow: 0 2px 4px 0  rgba(28, 140, 193, 0.7);
				    border-radius: 100px;
				    font-size: 14px;
				    color: #fff;
				    line-height: 40px;
				    text-align: center;
				}

			}
			.bottom-add, .bottom-buy {
				flex: 1;
			}
		}
	}
</style>


